@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Open Sans Light"), url("../fonts/opensans-light.woff2") format("woff2"), url("../fonts/opensans-light.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Open Sans SemiBold"), url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Open Sans Bold"), url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff"); }

/*Цвета*/
/*Media*/
a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  color: #000000; }

.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  max-width: 1440px;
  margin: 0 auto; }

.page-header {
  background-image: url("../../img/header_bike_desktop.jpg");
  background-repeat: no-repeat; }

.page-header__wrapper {
  max-width: 880px;
  margin: 0 auto; }

.page-header__logo {
  position: absolute;
  top: 318px; }

.promo {
  display: flex;
  justify-content: space-between;
  margin-bottom: 240px;
  color: #ffffff; }

.promo__text {
  max-width: 400px;
  padding-top: 56px; }

.promo__title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 46px;
  line-height: 64px; }

.promo__description {
  margin: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  opacity: 0.8; }

.promo__form {
  width: 327px; }

.main-nav {
  position: relative;
  margin-bottom: 251px; }

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 21px 25px 9px 21px;
  list-style: none; }

.main-nav__item {
  font-weight: 300; }

.main-nav__link {
  color: #ffffff;
  opacity: 0.7; }

.user-form__title {
  margin: 0;
  margin-bottom: 29px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center; }

.user-form__wrapper {
  display: flex;
  flex-direction: column; }

.user-form__input {
  padding: 14px 30px 10px;
  margin-bottom: 20px;
  border: 1px solid #C9CCD4;
  border-radius: 6px; }
  .user-form__input::placeholder {
    font-size: 15px;
    line-height: 24px;
    color: #1c1c1c;
    opacity: 0.5; }

.user-form__button {
  display: flex;
  width: 326px;
  height: 51px;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  background-color: #0ad9c6;
  border: none;
  border-radius: 6px; }
